<template>
    <div class="my">
        <div class="myTop">
            <van-search
                v-model="searchvalue"
                show-action
                shape="round"
                placeholder="请输入搜索关键词"
                input-align="center"
                left-icon
            >
                <template #action>
                    <div class="personal" v-if="personalimg">
                        <img :src="personalimg" alt />
                    </div>
                    <div class="nologin" v-else @click="goLogin">
                        <img src="../../assets/img/nologin.svg" alt />
                    </div>
                </template>
                <template #left>
                    <div class="loginOut" @click="outLogin"></div>
                </template>
            </van-search>
        </div>
        <div class="myBody">
            <div class="bodyTitle">
                <span>我的音乐</span>
            </div>
            <!-- 菜单列表 -->
            <div class="iconList">
                <div
                    class="iconListItem flexbetween"
                    v-for="(item, index) in iconListData"
                    :key="index"
                >
                    <div class="itemLeft flexbetween">
                        <div class="left_imgbox">
                            <img :src="item.iconUrl" alt />
                        </div>
                        <div class="left_title">
                            <span>{{ item.title }}</span>
                        </div>
                    </div>
                    <div class="itemRight flexbetween">
                        <div class="right_num">
                            <span>{{ item.num }}</span>
                        </div>
                        <div class="right_jT">
                            <van-icon name="arrow" size="18" />
                        </div>
                    </div>
                </div>
            </div>
            <!-- 歌单 -->
            <div class="songList">
                <van-tabs v-model="active">
                    <van-tab :title="'我的歌单' + myPlayList">
                        <div class="songListBox">
                            <div
                                class="songItem"
                                v-for="(item, index) in myPlayListData"
                                :key="index"
                                @click="clicksongitem(item.id)"
                            >
                                <div class="songItemImg">
                                    <img :src="item.coverImgUrl" alt />
                                </div>
                                <div class="songItemTitle">
                                    <div class="titleTop one-ellipsis">{{
                                        item.name
                                    }}</div>
                                    <div class="titleBottom"
                                        >{{ item.trackCount }}首</div
                                    >
                                </div>
                            </div>
                        </div>
                    </van-tab>
                    <van-tab :title="'收藏歌单' + liveNum">
                        <div class="songListBox">
                            <div
                                class="songItem"
                                v-for="(item, index) in myliveListData"
                                :key="index"
                                @click="clicksongitem(item.id)"
                            >
                                <div class="songItemImg">
                                    <img :src="item.coverImgUrl" alt />
                                </div>
                                <div class="songItemTitle">
                                    <div class="titleTop one-ellipsis">{{
                                        item.name
                                    }}</div>
                                    <div class="titleBottom"
                                        >{{ item.trackCount }}首</div
                                    >
                                </div>
                            </div>
                        </div>
                    </van-tab>
                </van-tabs>
                <!-- 右边的添加 -->
                <div class="addIcon">
                    <img src="../../assets/img/addflie.svg" alt />
                    <van-icon name="ellipsis" size="18" />
                </div>
            </div>
        </div>
        <div class="myFoot"></div>
    </div>
</template>

<script>
import "../../assets/less/mymusic.less";
import { createNamespacedHelpers } from "vuex";
import { setupCookie } from "../../assets/js/setupCookie";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    data() {
        return {
            active: 0,
            searchvalue: "",
            //头像
            personalimg: "",
            //收藏歌单数量,数据
            liveNum: 0,
            myliveListData: [],
            //我的歌单数量,数据
            myPlayList: 0,
            myPlayListData: [],
            //icon数据
            iconListData: [
                {
                    title: "本地音乐",
                    iconUrl: require("../../assets/img/local.svg"),
                    num: 15
                },
                {
                    title: "最近播放",
                    iconUrl: require("../../assets/img/recentlyplay.svg"),
                    num: 15
                },
                {
                    title: "我的电台",
                    iconUrl: require("../../assets/img/radiostation.svg"),
                    num: 15
                },
                {
                    title: "我的收藏",
                    iconUrl: require("../../assets/img/mylive.svg"),
                    num: 0
                }
            ]
        };
    },
    created() {
        //获取用户登录状态
        this.getuserStatus();
        //设置音乐组件
        if (this.detailbool == 1) {
            this.setaudioxy(0);
        }
        //获取用户信息
        this.getUserData();
        //获取用户歌单
        this.getUserSongListData();
    },
    computed: {
        ...musicState(["userId", "detailbool"])
    },
    methods: {
        ...musicActions(["setaudioxy"]),
        //跳转到登录
        goLogin() {
            this.$router.push({ name: "Login" });
        },

        //获取用户登录状态
        getuserStatus() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }
            this.axios({
                method: "GET",
                url: "/login/status",
                params: {
                    cookie
                }
            })
                .then(result => {
                    this.personalimg = result.data.profile.avatarUrl;
                })
                .catch(err => {});
        },

        //获取用户信息
        getUserData() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/user/subcount",
                params: {
                    cookie
                }
            })
                .then(result => {
                    this.$toast.clear();
                    //收藏数量
                    this.liveNum = result.data.subPlaylistCount;
                    this.iconListData[3].num = result.data.subPlaylistCount;
                    //我的歌单
                    this.myPlayList = result.data.createdPlaylistCount;
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取用户歌单
        getUserSongListData() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });

            // this.userId
            let uid = localStorage.getItem("ID");
            this.axios({
                method: "GET",
                url: "/user/playlist",
                params: {
                    uid
                }
            })
                .then(result => {
                    this.$toast.clear();
                    let playListData = result.data.playlist;
                    for (let i = 0; i < playListData.length; i++) {
                        if (playListData[i].userId != uid) {
                            this.myliveListData.push(playListData[i]);
                        } else {
                            this.myPlayListData.push(playListData[i]);
                        }
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //点击歌单跳转
        clicksongitem(id) {
            this.$router.push({
                name: "Listdetails",
                query: { id }
            });
        },

        //退出登录
        outLogin() {
            this.$dialog
                .confirm({
                    title: "提示",
                    message: "退出登录？"
                })
                .then(() => {
                    //删除cookie
                    setupCookie.removeCookies();
                    //删除id
                    localStorage.removeItem("ID");
                    this.$router.push({ name: "Tuijian" });
                })
                .catch(() => {
                    // on cancel
                });
        }
    }
};
</script>

<style lang="less" scoped>
</style>